.card .img {
    width: 210px;
    height: 210px;
    margin-bottom: 20px;
}

.card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.1s ease-in 0s;
}
.card h2 {
    font-size: 20px;
    line-height: 30px;
}
.card img:hover {
    transform: scale(1.05)
}